<template>
  <div>
    <div class="listTitle">
        <i></i>
        <span></span>
        <!-- <el-button
          class="fr"
          type="primary"
        @click="handleEdit(scope.$index, scope.row)"></el-button>-->
    </div>
    <el-table :data="tableData" border class="mb10" style="text-align:center" height="600" @selection-change="handleSelectionChange">
        <el-table-column fixed="left" type="selection"></el-table-column>
        <el-table-column prop="patientName" label=""></el-table-column>
        <el-table-column prop="reservationDate" label="">
            <template slot-scope="scope">
              <span>{{scope.row.reservationDate}}</span>
            </template>
        </el-table-column>
         <el-table-column prop="reservationDate" label="">
            <template slot-scope="scope">
              <span>{{scope.row.registeredDate}}</span>
            </template>
        </el-table-column>
        <el-table-column prop="reservationType" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.reservationType === 'RESERVATION'"></span>
            <span v-if="scope.row.reservationType === 'REGISTERED'"></span>
          </template>
        </el-table-column>
        <el-table-column prop="patientType" label="">
           <template slot-scope="scope">
            <span v-if="scope.row.patientType === 'PT'"></span>
            <span v-if="scope.row.patientType === 'LS'"></span>
          </template>
        </el-table-column>
        <el-table-column prop="reservationDoctorName" label=""></el-table-column>
        <el-table-column prop="serviceChooses" label="" min-width="150">
          <template slot-scope="scope">
             <span v-for="(item,index) in scope.row.serviceChooses" :key="item.code">
                {{item.projectPropertyName}}<span v-if="scope.row.serviceChooses.length -1 > index">/</span>
             </span>
          </template>
        </el-table-column>
        <el-table-column prop="visitingType" label="">
           <template slot-scope="scope">
            <span v-if="scope.row.visitingType === 'NEWDIAGNOSIS'"></span>
            <span v-if="scope.row.visitingType === 'REVIEW'"></span>
          </template>
        </el-table-column>
        <el-table-column prop="vistitingStatus" label="">
            <template slot-scope="scope">
              <span v-if="scope.row.vistitingStatus === 'UNCONFIRMED'"><span class="indicatorA"><span></span></span></span>
              <span v-if="scope.row.vistitingStatus === 'TREATMENT'"><span class="indicatorB"><span></span></span></span>
              <span v-if="scope.row.vistitingStatus === 'CANCEL'"><span class="indicatorC"><span></span></span></span>
              <span v-if="scope.row.vistitingStatus === 'FINISHED'"><span class="indicatorD"><span></span></span></span>
            </template>
        </el-table-column>
        <el-table-column prop="remark" label="" min-width="150"></el-table-column>

        <el-table-column label="" fixed="right" v-if="this.appVuex.menulist.find( item=> item.permission === 'VisitingForAdvisory')">
          <slot :row="scope.row" slot-scope="scope" name="Advisory"></slot>
        </el-table-column>
        <el-table-column label="" fixed="right" v-if=" this.appVuex.menulist.find( item=> item.permission === 'VsitingForDoctor')">
          <slot :row="scope.row" slot-scope="scope" name="Doctor"></slot>
        </el-table-column>
        <el-table-column label="" fixed="right">
          <slot :row="scope.row" slot-scope="scope" name="Collection"></slot>
        </el-table-column>
        <el-table-column label="" fixed="right" v-if="this.appVuex.menulist.find( item=> item.permission === 'Advisory')">
          <slot :row="scope.row" slot-scope="scope" name="bb"></slot>
        </el-table-column>
        <el-table-column label="" fixed="right" v-if="this.appVuex.menulist.find( item=> item.permission === 'Triage')">
          <slot :row="scope.row" slot-scope="scope" name="cc"></slot>
        </el-table-column>
        <el-table-column label="" fixed="right" v-if="this.appVuex.menulist.find( item=> item.permission === 'Referral')">
          <slot :row="scope.row" slot-scope="scope" name="ff"></slot>
        </el-table-column>
        <el-table-column label="" fixed="right" v-if="this.appVuex.menulist.find( item=> item.permission === 'Medical')">
          <slot :row="scope.row" slot-scope="scope" name="gg"></slot>
        </el-table-column>
        <el-table-column label="" fixed="right" v-if="this.appVuex.menulist.find( item=> item.permission === 'PatientReservation')">
          <slot :row="scope.row" slot-scope="scope" name="dd"></slot>
        </el-table-column>
        <el-table-column label="" fixed="right" v-if="this.appVuex.menulist.find( item=> item.permission === 'Finished')">
          <slot :row="scope.row" slot-scope="scope" name="ee"></slot>
        </el-table-column>
        
      </el-table>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  components:{},
  props:{
    tableData: {
      type: Array,
      default: []
    },
    tabConKeyName: {
      type: String,
      default: ''
    },
  },
  data(){
    return {
       roleEnnames: '',
    }
  },
  watch:{},
  computed:{
    ...mapState({
      appVuex: state => state["appVuex"]
    })
  },
  methods:{
    handleSelectionChange() {
      this.$emit('handleSelectionChange')
    }
  },
  created(){},
  mounted(){
  }
}
</script>
<style lang="less" scoped>

</style>


